/* eslint-disable no-plusplus */
export default {
  RequiredRules() {
    return [(v) => !!v || "Campo obrigatório"];
  },

  NameRules() {
    return [
      (v) => !!v || "Por favor preencha o nome",
      (v) =>
        (v && v.length >= 3) || "O nome precisa ter no mínimo 3 caracteres",
      (v) => (v && v.length <= 255) || "O limite máximo é de 255 caracteres",
    ];
  },

  LastNameRules() {
    return [
      (v) => !!v || "Por favor preencha o sobrenome",
      (v) =>
        (v && v.length >= 3) ||
        "O sobrenome precisa ter no mínimo 3 caracteres",
      (v) => (v && v.length <= 255) || "O limite máximo é de 255 caracteres",
    ];
  },

  EmailRules() {
    return [
      (v) => !!v || "Por favor preencha um email",
      (v) => this.Email(v) || "Por favor preencha um email válido",
      (v) => (v && v.length <= 255) || "O limite máximo é de 255 caracteres",
    ];
  },

  PasswordRules() {
    return [
      (v) => !!v || "Por favor preencha uma senha",
      (v) =>
        this.Password(v) ||
        "A senha precisa ter no mínimo 8 caracteres, letra maiúscula, minúscula, número e caracter especial",
      (v) => (v && v.length <= 30) || "O limite máximo é de 30 caracteres",
    ];
  },

  PhoneRules() {
    return [(v) => !!v || "Por favor preencha um telefone"];
  },

  CpfRules() {
    return [
      (v) => !!v || "Por favor preencha um CPF",
      (v) => (v && this.CPF(v)) || "Por favor preencha um CPF válido",
      (v) => (v && v.length <= 14) || "O limite máximo é de 14 caracteres",
    ];
  },

  SmsCodeRules() {
    return [
      (v) => !!v || "Por favor preencha seu código",
      (v) =>
        (v && /\b\d{5}\b/g.test(v)) || "Por favor preencha um código válido",
    ];
  },

  OrderRules() {
    return [(v) => !!v || "Por favor informe a ordem"];
  },

  Email(string) {
    const re = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return re.test(string);
  },

  Birthday(string) {
    const re = /^([0-9]{2})\/([0-9]{2})\/([0-9]{4})$/;
    return re.test(string);
  },

  Password(string) {
    const re = /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/;
    return re.test(string);
  },

  CPF(string) {
    let Soma;
    let Resto;
    Soma = 0;
    string = string.replace(/[^\d]+/g, "");
    if (
      string === "00000000000" ||
      string === "11111111111" ||
      string === "22222222222" ||
      string === "33333333333" ||
      string === "44444444444" ||
      string === "55555555555" ||
      string === "66666666666" ||
      string === "77777777777" ||
      string === "88888888888" ||
      string === "99999999999"
    )
      return false;

    for (let i = 1; i <= 9; i++)
      Soma += parseInt(string.substring(i - 1, i)) * (11 - i);
    Resto = (Soma * 10) % 11;

    if (Resto === 10 || Resto === 11) Resto = 0;

    if (Resto !== parseInt(string.substring(9, 10))) return false;

    Soma = 0;
    for (let i = 1; i <= 10; i++)
      Soma += parseInt(string.substring(i - 1, i)) * (12 - i);
    Resto = (Soma * 10) % 11;

    if (Resto === 10 || Resto === 11) Resto = 0;

    if (Resto !== parseInt(string.substring(10, 11))) return false;

    return true;
  },

  CNPJ(string) {
    let resultado = "";
    let cnpj = string.replace(/[^\d]+/g, "");
    if (cnpj === "") return false;

    if (cnpj.length !== 14) return false;

    if (
      cnpj === "00000000000000" ||
      cnpj === "11111111111111" ||
      cnpj === "22222222222222" ||
      cnpj === "33333333333333" ||
      cnpj === "44444444444444" ||
      cnpj === "55555555555555" ||
      cnpj === "66666666666666" ||
      cnpj === "77777777777777" ||
      cnpj === "88888888888888" ||
      cnpj === "99999999999999"
    )
      return false;

    let tamanho = cnpj.length - 2;
    let numeros = cnpj.substring(0, tamanho);
    const digitos = cnpj.substring(tamanho);
    let soma = 0;
    let pos = tamanho - 7;
    for (let i = tamanho; i >= 1; i--) {
      soma += numeros.charAt(tamanho - i) * pos--;
      if (pos < 2) pos = 9;
    }
    resultado = soma % 11 < 2 ? 0 : 11 - (soma % 11);
    if (resultado !== digitos.charAt(0)) return false;

    tamanho += 1;
    numeros = cnpj.substring(0, tamanho);
    soma = 0;
    pos = tamanho - 7;
    for (let i = tamanho; i >= 1; i--) {
      soma += numeros.charAt(tamanho - i) * pos--;
      if (pos < 2) pos = 9;
    }
    resultado = soma % 11 < 2 ? 0 : 11 - (soma % 11);
    if (resultado !== digitos.charAt(1)) return false;

    return true;
  },
};
