import Vue from "vue";
import VueRouter from "vue-router";
import LocalStorage from "@/utils/helpers/localStorage";
import routes from "./routes";

Vue.use(VueRouter);

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach((to, from, next) => {
  if (
    to.path.match("restricted") !== null &&
    to.path !== "/restricted/login" &&
    !LocalStorage.get("authToken")
  ) {
    next("/restricted/login");
    return false;
  }

  if (
    (to.path === "/restricted" || to.path === "/restricted/") &&
    LocalStorage.get("authToken")
  ) {
    next("/restricted/dashboard");
    return false;
  }

  return next();
});

router.afterEach(() => {
  if (window.scroll) {
    window.scroll({
      top: 0,
      left: 0,
      behavior: "smooth",
    });
  }
});

export default router;
