const set = (key, value) => {
  localStorage.setItem(
    key,
    JSON.stringify({
      value,
    }),
  );
};

const get = (key) => {
  const data = localStorage.getItem(key);
  if (data) {
    return JSON.parse(data).value;
  }
  return null;
};

const del = (key) => {
  localStorage.removeItem(key);
};

export default { set, get, del };
